<template>
  <div class="singerSong" v-on:mouseenter="toggle(isShow)" v-on:mouseleave="toggle(isShow)">
    <div class="index" v-if="singerSong.index < 4">{{singerSong.index}}</div>
    <div class="index" v-else>{{singerSong.index}}</div>
    <div class="name">
      {{singerSong.name}}
      <span v-if="isShow">
        <span class="icon-play2" title="播放"></span>
        &nbsp;&nbsp;
        <span class="icon-plus" title="添加到歌单"></span>
        &nbsp;&nbsp;
        <span class="icon-download3" title="下载"></span>
      </span>
    </div>
    <div class="singer">{{singerSong.singer}}</div>
    <div class="time">{{singerSong.time}}</div>
  </div>
</template>
<script>
  export default{
    data () {
      return {
        isShow: false
      }
    },
    props: ['singerSong'],
    methods: {
      toggle (flag) {
        if (flag) {
          this.isShow = false
        } else {
          this.isShow = true
        }
      }
    }
  }
</script>
<style scoped>
  .singerSong{
    display: flex;
    height: 50px;
    margin: 5px 0;
  }
  .singerSong > div{
    text-align: center;
    margin: 10px;
    vertical-align: middle;
    display: table-cell;
    line-height: 80px;
  }
  .singerSong > .index{
    flex: 1;
    font-size: 25px;
    color: #999999;
  }
  .singerSong > .name{
    flex: 4;
    text-align: left;
    position: relative;
  }
  .singerSong > .name > span{
    position: absolute;
    right: 0;
    color: silver;
    font-size: 20px;
  }
  .singerSong > .name > span >span{
    cursor: pointer;
  }
  .singerSong > .singer{
    flex: 3;
  }
  .singerSong > .time{
    flex: 2;
  }
</style>



