<template>
  <div id="modal" v-if="isShowModal.state">
    <form action="#" id="modalLogin">
      <div id="login">
        <a href="javascript:;" class="close" v-on:click="closeModal"><i class="icon-cross"></i></a>
        <label for="name">账号</label>
        <input id="name"/>
        <br />
        <label for="password">密码</label>
        <input type="password" id="password"/>
        <br/>
        <a href="javascript:;" class="login" v-on:click="submit">登录</a>
        <br />
        <a href="javascript:;" class="register">没有账号？点此注册<<</a>
      </div>
    </form>
  </div>
</template>
<script>
  export default {
    name: 'navigation',
    methods: {
      submit: function () {
        document.getElementById('modalLogin').submit()
      },
      closeModal: function () {
        this.isShowModal.state = false
      }
    },
    data () {
      return {
      }
    },
    props: ['isShowModal']
  }
</script>
<style scoped>
  #modal{
    position: fixed;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    background-color: rgba(212,212,212,.5);
  }
  #modal > form > #login{
    width: 300px;
    height: 160px;
    position: fixed;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    border: 1px white solid;
    background-color: white;
    margin: auto;
    text-align: center;
    padding: 50px;
    line-height: 40px;
    font-size: 18px;

  }
  #modal > form > #login > .close{
    position: absolute;
    top: 0;
    right: 0;
    font-size: 20px;
    margin-top: -10px;
    text-decoration: none;
    color: rgba(105,105,105,.7);
  }
  #modal > form > #login > .close:hover{
    color: rgba(105,105,105,1);
  }
  #modal > form > #login > input{
    height: 25px;
    border-radius: 5px;
    outline: none;
    font-size: 16px;
    border: 1px solid #cccccc;
  }
  #modal > form > #login > .login{
    width: 200px;
    display: inline-block;
    background-color: rgba(21,143,225,.8);
    text-decoration: none;
    color: white;
  }
  #modal > form > #login > .login:hover{
    background-color: rgba(21,143,225,1);
  }
  #modal > form > #login > .register{
    font-size: 12px;
    margin-left: 100px;
    text-decoration: none;
    color: #555555;
  }
</style>



