<template>
  <div class="play">
    <div class="head">
      <div class="image"><img src="/static/image/playMusic1.jpg" width="100%"></div>
      <div class="intro">
        <h1>{{song.name}}</h1>
        <div class="text">{{song.author}}</div>
        <div class="text">专辑：{{song.album}}</div>
        <div class="text">语种：{{song.language}}</div>
        <div class="text">发行时间：{{song.time}}</div>
        <div class="btn">
          <a class="playbtn"><i class="icon-play2"></i>播放</a>
          <a>更多</a>
        </div>
      </div>
    </div>
    <div>geci</div>
    <footer></footer>
  </div>
</template>
<script>
export default{
  data () {
    return {
      song: {
        name: '背过手',
        author: '薛之谦',
        album: '渡',
        language: '国语',
        time: '2017-11-28'
      }
    }
  }
}
</script>
<style scoped>
  .play{
    margin: 0 100px;
  }
  .play > .head{
    display: flex;
    margin: 50px 0;
  }
  .play > .head > .image{
    width: 250px;
    height: 250px;
  }
  .play > .head > .intro{
    margin-left: 50px;
  }
  .play > .head > .intro > .text{
    font-size: 15px;
    color: #555555;
  }
  .play > .head > .intro > .btn{
    margin-top: 30px;
  }
  .play > .head > .intro > .btn >a{
    cursor: pointer;
    width: 120px;
    height: 30px;
    display: inline-block;
    border: 1px solid #cccccc;
    text-align: center;
    line-height: 30px;
    border-radius: 5px;
    font-size: 18px;
  }
  .play > .head > .intro > .btn >a:hover{
    background-color: rgb(237,237,237);
  }
  .play > .head > .intro > .btn >.playbtn{
    background-color: rgb(49,194,124);
    color: white;
  }
  .play > .head > .intro > .btn >.playbtn:hover{
    background-color: rgb(44,175,111);
  }
  footer{
    height: 100px;
    margin: 0 -100px;
    background-color: rgb(51,51,51);
  }

</style>



