<template>
  <div class="songs" v-on:mouseenter="toggleArrow" v-on:mouseleave="toggleArrow">
    <a href="javascript:;" class="icon-circle-left iconLeft" v-if="!arrow" v-on:click="moveLeft"></a>
    <a href="javascript:;" class="icon-circle-left iconLeft iconLeftShow" v-else v-on:click="moveLeft"></a>
    <a href="javascript:;" class="icon-circle-right iconRight" v-if="!arrow" v-on:click="moveRight"></a>
    <a href="javascript:;" class="icon-circle-right iconRight iconRightShow" v-else v-on:click="moveRight"></a>
    <div class="pagebox">
      <div v-bind:class="pageClass" >
        <song ></song>
        <song ></song>
        <song ></song>
        <song ></song>
        <song ></song>
        <song ></song>
      </div>
      <div v-bind:class="pageClass" >
        <song ></song>
        <song ></song>
        <song ></song>
        <song ></song>
        <song ></song>
        <song ></song>
      </div>
      <div v-bind:class="pageClass" >
        <song ></song>
        <song ></song>
        <song ></song>
        <song ></song>
        <song ></song>
        <song ></song>
      </div>
      <div class="circleControl">
        <a v-for="index in arrPage" class="currentCircleControl" v-if="index===currentPage-1" v-on:click="toggleGedan(index)"></a>
        <a v-else v-on:click="toggleGedan(index)"></a>
      </div>
    </div>
  </div>
</template>
<script>
import song from './song.vue'
export default{
  data () {
    return {
      arrow: false,
      currentPage: 1,
      totalPage: 2,
      arrPage: [],
      pageClass: 'page'
    }
  },
  mounted () {
    for (let i = 0; i < this.totalPage; i++) {
      this.arrPage.push(i)
    }
  },
  props: ['current', 'totalGedan'],
  components: {
    song
  },
  methods: {
    toggleArrow: function () {
      if (this.arrow === false) {
        this.arrow = true
      } else {
        this.arrow = false
      }
    },
    toggleGedan: function (index) {
      this.currentPage = index + 1
      this.pageClass = 'page page' + this.currentPage
    },
    moveLeft: function () {
      this.currentPage--
      if (this.currentPage === 0) {
        this.currentPage = this.totalPage
      }
      this.pageClass = 'page page' + this.currentPage
    },
    moveRight: function () {
      this.currentPage++
      if (this.currentPage === this.totalPage + 1) {
        this.currentPage = 1
      }
      this.pageClass = 'page page' + this.currentPage
    }
  }
}
</script>
<style scoped>
  .songs{
    padding: 10px 5%;
    width: 100%;
  }
  .songs > .pagebox{
    width: 90%;
    overflow: hidden;
    height: 420px;
  }
  .songs  >.pagebox> .page{
    display: flex;
    width: 200%;
    margin-left: 0;
    transition: all 1s;
  }
  .songs  >.pagebox> .page1{
    margin-left: 0;
  }
  .songs  >.pagebox> .page2{
    margin-left: -100%;
  }
  .songs > .pagebox > .circleControl{
    position: absolute;
    bottom: 5%;
    left: 50%;
    margin-left: -25px;
  }
  .songs > .pagebox > .circleControl > a{
    cursor: pointer;
    display: inline-block;
    width: 15px;
    height: 15px;
    border: 1px solid rgb(49,194,124);
    border-radius: 50%;
    margin: 5px;

  }
  .songs > .pagebox > .circleControl > .currentCircleControl{
    background-color: rgb(49,194,124);
  }
  .songs > .iconLeft, .songs > .iconRight{
    font-size: 50px;
    color: #cccccc;
    position: absolute;
    top: 50%;
    margin-top: -25px;
    transition: all 1s;
    text-decoration: none;
  }
  .songs > .iconLeft{
    left: -50px;
  }
  .songs > .iconLeftShow{
    left: 0;
  }
  .songs > .iconRight{
    right: -50px;
  }
  .songs > .iconRightShow{
    right: 0;
  }
</style>



