<template>
  <div class="singerMessage">
    <div class="picture">
      <img v-bind:src="singerData.picture" width="100%" height="100%">
    </div>
    <div class="box">
      <h1>{{singerData.name}}</h1>
      <span class="intro">{{singerData.intro}}</span>
      <span class="more">[更多]</span>

      <div class="works">
        <a v-on:click="toggle('单曲')">单曲&nbsp;&nbsp;<span>{{singerData.songsNum}}</span></a>
          &nbsp;&nbsp;|&nbsp;&nbsp;
        <a v-on:click="toggle('专辑')">专辑&nbsp;&nbsp;<span>{{singerData.albumsNum}}</span></a>
          &nbsp;&nbsp;|&nbsp;&nbsp;
        <a v-on:click="toggle('MV')">MV&nbsp;&nbsp;<span>{{singerData.MVsNum}}</span></a>
      </div>
    </div>
    <h2>{{current}}</h2>
    <div class="song" v-if="current === '单曲'">
      <div class="head">
        <span class="name">歌曲</span><span class="album">专辑</span><span class="time">时长</span>
      </div>
      <singerSong v-bind:singerSong="singerSong[0]"></singerSong>
      <singerSong v-bind:singerSong="singerSong[0]"></singerSong>
      <page v-bind:page="page"></page>
    </div>
    <div class="album" v-if="current === '专辑'">
      <div class="albumbox">
        <album v-bind:album="album[0]"></album>
        <album v-bind:album="album[0]"></album>
        <album v-bind:album="album[0]"></album>
        <album v-bind:album="album[0]"></album>
        <album v-bind:album="album[0]"></album>
        <album v-bind:album="album[0]"></album>
        <album v-bind:album="album[0]"></album>
        <album v-bind:album="album[0]"></album>
      </div>
      <page v-bind:page="page"></page>
    </div>

    <div class="MV" v-if="current === 'MV'">
      <div class="MVbox">
        <MV v-bind:mv="MVs[0]"></MV>
        <MV v-bind:mv="MVs[0]"></MV>
        <MV v-bind:mv="MVs[0]"></MV>
        <MV v-bind:mv="MVs[0]"></MV>
        <MV v-bind:mv="MVs[0]"></MV>
        <MV v-bind:mv="MVs[0]"></MV>
        <MV v-bind:mv="MVs[0]"></MV>
        <MV v-bind:mv="MVs[0]"></MV>
      </div>
      <page v-bind:page="page"></page>
    </div>

    <footer></footer>
  </div>
</template>
<script>
import singerSong from './singerSong.vue'
import page from './page.vue'
import album from './album.vue'
import MV from './mv.vue'
export default{
  data () {
    return {
      singerData: {
        name: '周杰伦',
        picture: '/static/image/singerPic1.jpg',
        intro: '周杰伦（Jay Chou），中国台湾流行乐男歌手音乐人中国台湾流行乐男歌手音乐人中国台湾流行乐男歌手音乐人，中国台湾流行乐男歌手音乐人、演员、导演、编剧、监制、商人。',
        songsNum: 394,
        albumsNum: 30,
        MVsNum: 796
      },
      current: 'MV',
      page: {total: 50, current: 1},
      singerSong: [{
        index: 1,
        name: '告白气球',
        singer: '周杰伦的床边故事',
        time: '04:07'}],
      album: [{name: '周杰伦的床边故事', author: '周杰伦', time: '2017-11-23', src: '/static/image/album1.jpg'}],
      MVs: [{name: '戒烟', author: '李荣浩', popular: '28w', time: '2017-11-20', src: './staticmvv=MVs'}]
    }
  },
  components: {
    singerSong,
    page,
    album,
    MV
  },
  methods: {
    toggle (str) {
      this.current = str
    }
  }
}
</script>
<style scoped>
  .singerMessage{
    margin: 0 100px;
  }
  .singerMessage > .picture{
    display: inline-block;
    border-radius: 50%;
    border: 1px solid transparent;
    overflow: hidden;
    width: 250px;
    height: 250px;
  }

  .singerMessage > .box{
    display: inline-block;
    vertical-align: top;
    width: 60%;
    margin-left: 50px;
  }
  .singerMessage > .box > .intro{
    width: 90%;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
  }
  .singerMessage > .box > .more{
    cursor: pointer;
  }
  .singerMessage > .box > .more:hover{
    color: rgb(49,194,124);
  }
  .singerMessage > .box >span{
    display: inline-block;
    vertical-align: top;
    font-size: 15px;
    margin: 10px 0 ;
  }
  .singerMessage > .box > .works{
    font-size: 20px;
  }
  .singerMessage > .box > .works >a{
    cursor: pointer;
  }
  .singerMessage > .box > .works >a:hover{
    color: rgb(49,194,124);
  }
  .singerMessage > .box > .works >a> span{
    font-size: 28px;
  }
  .singerMessage > .song > .head{
    display: flex;
    color: #999999;
    font-size: 15px;
  }
  .singerMessage > .song > .head>.name{
    padding-left: 12%;
    flex: 4;
  }
  .singerMessage > .song > .head>.album{
    flex: 3;
    padding-left: 23%;
  }
  .singerMessage > .song > .head>.time{
    flex: 2;
    padding-left: 10%;
  }
  .singerMessage > h2{
    margin: 20px;
  }
  footer{
    height: 100px;
    background-color: rgb(51,51,51);
    margin: 0 -100px;
  }
  .singerMessage > .album>.albumbox{
    display: flex;
    flex-wrap: wrap;

  }
  .singerMessage > .MV>.MVbox{
    display: flex;
    flex-wrap: wrap;

  }
</style>



