<template>
  <div class="mv">
    <a href="javascript:;" v-on:mouseenter="scaleImg" v-on:mouseleave="scaleImg2">
      <img src="/static/image/mv1.jpg" v-bind:class="hoverImg" width="100%" height="100%">
      <i class="icon-play2"></i>
    </a>
    <div class="name">{{mv.name}}</div>
    <div class="author">{{mv.author}}</div>
    <div class="other"><i class="icon-video-camera"></i>&nbsp;&nbsp;{{mv.popular}}
      &nbsp;&nbsp;&nbsp;&nbsp;{{mv.time}}</div>
  </div>
</template>
<script>
  export default{
    data () {
      return {
        hoverImg: 'notHoverImg'
      }
    },
    props: ['mv'],
    methods: {
      scaleImg: function () {
        this.hoverImg = 'notHoverImg HoverImg'
      },
      scaleImg2: function () {
        this.hoverImg = 'notHoverImg'
      }
    }
  }
</script>
<style scoped>
  .mv{
    width: 23%;
    margin: 10px;
  }
  .mv > a{
    display: block;
    width: 100%;
    height: 160px;
    position: relative;
    overflow: hidden;
  }
  .mv> a > i {
    color: silver;
    font-size: 4rem;
    position: absolute;
    top: 50%;
    left: 50%;
    margin-left: -2rem;
    margin-top: -2rem;
    transform: scale(0.01);
    transition: all 1s;
  }
  .mv> a:hover > i{
    transform: scale(1);
  }
  .mv> a > .notHoverImg{
    transition: all 1s;

  }
  .mv> a > .HoverImg{
    transform: scale(1.2);
  }
  .mv > .name, .mv > .author, .mv > .other{
    font-size: 15px;
  }
  .mv > .name{
    color: #555555;
  }
  .mv > .author, .mv > .other{
    color: rgb(153,153,153);
  }
</style>



