<template>
  <div id="album">
    <classify v-bind:classify="classify.type1" v-bind:current="current[0]"></classify>
    <classify v-bind:classify="classify.type2" v-bind:current="current[1]"></classify>
    <h2>全部专辑</h2>
    <div class="albums">
      <album v-bind:album="album[0]"></album>
      <album v-bind:album="album[0]"></album>
      <album v-bind:album="album[0]"></album>
      <album v-bind:album="album[0]"></album>
      <album v-bind:album="album[0]"></album>

      <album v-bind:album="album[0]"></album>
      <album v-bind:album="album[0]"></album>
      <album v-bind:album="album[0]"></album>
      <album v-bind:album="album[0]"></album>
      <album v-bind:album="album[0]"></album>
    </div>
    <page v-bind:page="page"></page>
    <footer></footer>
  </div>
</template>
<script>
import classify from './singer/classify.vue'
import album from './singer/album.vue'
import page from './singer/page.vue'
export default{
  data () {
    return {
      classify: {
        type1: {
          title: '语种',
          content: ['全部', '国语', '粤语', '英语', '韩语', '日语', '其他']},
        type2: {
          title: '流派',
          content: ['全部', '流行', '古典', '爵士', '摇滚', '电子', '轻音乐', '舞曲', '民谣', '其他']}
      },
      current: [{cur: '全部'}, {cur: '全部'}],
      page: {total: 50, current: 1},
      album: [{name: '做回自己', author: '张靓颖', time: '2017-11-23', src: '/static/image/album1.jpg'}]
    }
  },
  components: {
    classify,
    album,
    page
  }
}
</script>
<style scoped>
  #album{
    margin: 0 100px;
  }
  #album> .albums{
    display: flex;
    flex-wrap: wrap;
  }
  footer{
    height: 100px;
    margin: 0 -100px;
    background-color: rgb(51,51,51);
  }
</style>



