<template>
  <div  id="homepage">
    <div id="recommend">
      <mytitle title="歌单推荐"></mytitle>
      <nav3 v-bind:title="navTitle.recommend" v-bind:current="current"></nav3>
      <gedan v-bind:current="current" v-bind:totalGedan="totalGedan"></gedan>
    </div>
    <div id="newSong">
      <mytitle title="新歌首发"></mytitle>
      <nav3 v-bind:title="navTitle.newSong" v-bind:current="current"></nav3>
      <songs></songs>
    </div>
    <div id="newAlbum">
      <mytitle title="新碟首发"></mytitle>
      <nav3 v-bind:title="navTitle.newAlbum" v-bind:current="current"></nav3>
      <albums></albums>
    </div>
    <div id="rank">
      <mytitle title="排行榜"></mytitle>
      <ranks></ranks>
    </div>
    <div id="mvs">
      <mytitle title="MV"></mytitle>
      <nav3 v-bind:title="navTitle.newSong" v-bind:current="current"></nav3>
      <mvs></mvs>
    </div>
    <footer></footer>
  </div>
</template>
<script>
import mytitle from './homePage/title.vue'
import nav3 from './homePage/nav3.vue'
import gedan from './homePage/gedan.vue'
import songs from './homePage/songs.vue'
import albums from './homePage/albums.vue'
import ranks from './homePage/ranks.vue'
import mvs from './homePage/mvs.vue'
export default{
  data () {
    return {
      navTitle: {
        recommend: ['为你推荐', '劲爆DJ', '夜店', '90年代', '英语', '城市'],
        newSong: ['内地', '港台', '欧美', '日本', '韩国'],
        newAlbum: ['内地', '港台', '欧美', '日本', '韩国'],
        mv: ['精选', '内地', '港台', '欧美', '日本', '韩国']
      },
      current: {
        num: 0
      },
      totalGedan: [
        {src: '/static/image/gedan1.jpg', description: '描述1', popularity: '人气'},
        {src: '/static/image/gedan2.jpg', description: '描述2', popularity: '人气'},
        {src: '/static/image/gedan3.jpg', description: '描述3', popularity: '人气'},
        {src: '/static/image/gedan4.jpg', description: '描述4', popularity: '人气'},
        {src: '/static/image/gedan5.jpg', description: '描述5', popularity: '人气'},
        {src: '/static/image/gedan6.jpg', description: '描述6', popularity: '人气'},
        {src: '/static/image/gedan7.jpg', description: '描述7', popularity: '人气'},
        {src: '/static/image/gedan8.jpg', description: '描述8', popularity: '人气'},
        {src: '/static/image/gedan9.jpg', description: '描述9', popularity: '人气'},
        {src: '/static/image/gedan10.jpg', description: '描述0', popularity: '人气'}
      ],
      arrow: false
    }
  },
  components: {
    mytitle,
    nav3,
    gedan,
    songs,
    albums,
    ranks,
    mvs
  },
  methods: {
  }
}
</script>
<style scoped>
  #recommend,#newSong,#newAlbum,#mvs{
    position: relative;
    height: 550px;
    background: linear-gradient(rgb(242,242,242), white);
    overflow: hidden;
    margin-top: 0;
  }
  #newAlbum{
    height: 800px;
  }
  #rank{
    background: linear-gradient(rgb(242,242,242), white);
  }
  #mvs{
    height: 600px;
  }
  footer{
    height: 100px;
    background-color: rgb(51,51,51);
  }
</style>



