<template>
  <div class="gedan">
    <div class="head">
      <div class="image"><img src="/static/image/playMusic1.jpg" width="100%"></div>
      <div class="intro">
        <h1>{{gedan.name}}</h1>
        <div class="text">标签：{{gedan.type}}</div>
        <div class="text">播放量：{{gedan.playTimes}}</div>
        <div class="btn">
          <a class="playbtn"><i class="icon-play2"></i>播放</a>
          <a>更多</a>
        </div>
      </div>
    </div>
    <div class="content">
      <div class="head">
        <span class="name">歌曲</span>
        <span class="singer">歌手</span>
        <span class="album">专辑</span>
        <span class="time">时长</span>
      </div>
      <gedanSong v-bind:gedanSong="gedanSong[0]"></gedanSong>
    </div>
    <footer></footer>
  </div>
</template>
<script>
  import gedanSong from './gedanSong.vue'
  export default{
    data () {
      return {
        gedan: {
          name: '想用歌声说爱你',
          type: '国语',
          playTimes: '播放量'
        },
        gedanSong: [{index: 1,
          name: '天后（Live）',
          singer: '薛之谦',
          album: '专辑',
          time: '04:07'}]
      }
    },
    components: {
      gedanSong
    }
  }
</script>
<style scoped>
  .gedan{
    margin: 0 100px;
  }
  .gedan > .head{
    display: flex;
    margin: 50px 0;
  }
  .gedan > .head > .image{
    width: 250px;
    height: 250px;
  }
  .gedan > .head > .intro{
    margin-left: 50px;
  }
  .gedan > .head > .intro > .text{
    font-size: 15px;
    color: #555555;
  }
  .gedan > .head > .intro > .btn{
    margin-top: 30px;
  }
  .gedan > .head > .intro > .btn >a{
    cursor: pointer;
    width: 120px;
    height: 30px;
    display: inline-block;
    border: 1px solid #cccccc;
    text-align: center;
    line-height: 30px;
    border-radius: 5px;
    font-size: 18px;
  }
  .gedan > .head > .intro > .btn >a:hover{
    background-color: rgb(237,237,237);
  }
  .gedan > .head > .intro > .btn >.playbtn{
    background-color: rgb(49,194,124);
    color: white;
  }
  .gedan > .head > .intro > .btn >.playbtn:hover{
    background-color: rgb(44,175,111);
  }
  .gedan > .content{
    text-align: center;
  }
  .gedan > .content > .head{
    display: flex;
    color: #999999;
    font-size: 15px;
  }
  .gedan > .content > .head > .name{
    flex: 4;
  }
  .gedan > .content > .head > .singer{
    flex: 2;
  }
  .gedan > .content > .head > .album{
    flex: 2;
  }
  .gedan > .content > .head > .time{
    flex: 1;
  }
  footer{
    height: 100px;
    margin: 0 -100px;
    background-color: rgb(51,51,51);
  }

</style>



