<template>
  <div class="nav3">
    <span></span>
    <a href="javascript:;" v-for="(item, index) in title" v-if="index===current.num" class="current"
       v-on:click="toggle(index)"><span>{{item}}</span></a>
    <a href="javascript:;" v-else v-on:click="toggle(index)"><span>{{item}}</span></a>
    <span></span>
  </div>
</template>
<script>
  export default{
    props: ['title', 'current'],
    data () {
      return {
      }
    },
    methods: {
      toggle: function (flag) {
        if (this.current.num !== flag) {
          this.current.num = flag
        }
      }
    }
  }
</script>
<style scoped>
  .nav3{
    height: 30px;
    line-height: 30px;
    display: flex;
  }
  .nav3 > a{
    flex: 1;
    text-align: center;
    font-size: 15px;
    text-decoration: none;
    color: #555555;
  }
  .nav3 > a> span:hover{
    color: rgb(49,194,124);
  }
  .nav3 > .current{
    color: rgb(49,194,124);
  }
  .nav3 > span{
    flex: 4;
  }
</style>



