<template>
  <div id="MV">
    <classify v-bind:classify="classify.type1" v-bind:current="current[0]"></classify>
    <classify v-bind:classify="classify.type2" v-bind:current="current[1]"></classify>
    <classify v-bind:classify="classify.type3" v-bind:current="current[2]"></classify>
    <h2>全部MV</h2>
    <div class="mvs">
      <mv v-bind:mv="MVs[0]"></mv>
      <mv v-bind:mv="MVs[0]"></mv>
      <mv v-bind:mv="MVs[0]"></mv>
      <mv v-bind:mv="MVs[0]"></mv>
      <mv v-bind:mv="MVs[0]"></mv>
      <mv v-bind:mv="MVs[0]"></mv>
      <mv v-bind:mv="MVs[0]"></mv>
      <mv v-bind:mv="MVs[0]"></mv>
    </div>
    <page v-bind:page="page"></page>
    <footer></footer>
  </div>

</template>
<script>
  import classify from './singer/classify.vue'
  import page from './singer/page.vue'
  import mv from './singer/mv.vue'
  export default{
    data () {
      return {
        classify: {
          type1: {
            title: '地区',
            content: ['全部', '内地', '港台', '欧美', '韩国', '日本', '其他']},
          type2: {
            title: '类型',
            content: ['全部', '官方版', '影视原声', '舞蹈', '演唱会', '动漫', '民族风', '戏曲', '儿歌',
              '搞笑', '鬼畜', '剧情', '弹奏', '游戏', 'KTV版', '广告', '景色', '其他']},
          type3: {
            title: '年份',
            content: ['全部', '2017', '2016', '2015', '2014', '2013', '2012', '2011', '00年代', '其他']}
        },
        current: [{cur: '全部'}, {cur: '全部'}, {cur: '全部'}],
        page: {total: 50, current: 1},
        MVs: [{name: '戒烟', author: '李荣浩', popular: '28w', time: '2017-11-20', src: './static/image/mv1.jpg'}]
      }
    },
    components: {
      classify,
      page,
      mv
    }
  }
</script>
<style scoped>
  #MV{
    margin: 0 100px;
  }
  #MV > .mvs{
    display: flex;
    flex-wrap: wrap;
  }
  footer{
    height: 100px;
    margin: 0 -100px;
    background-color: rgb(51,51,51);
  }

</style>



