<template>
  <div class="song">
    <a href="javascript:;" v-on:mouseenter="scaleImg" v-on:mouseleave="scaleImg2">
      <img src="/static/image/song1.jpg" v-bind:class="hoverImg">
      <i class="icon-play2"></i>
    </a>
    <span>
      <a class="name" href="javascript:;">name</a>
      <span class="author" >author</span>
      <span class="time">time</span>
    </span>
  </div>
</template>
<script>
  export default{
    data () {
      return {
        hoverImg: 'notHoverImg'
      }
    },
    props: ['gedan'],
    methods: {
      scaleImg: function () {
        this.hoverImg = 'notHoverImg HoverImg'
      },
      scaleImg2: function () {
        this.hoverImg = 'notHoverImg'
      }
    }
  }
</script>
<style scoped>
  .song{
    flex: 1;
    margin: 10px 0;
  }
  .song > a{
    position: relative;
    overflow: hidden;
    text-decoration: none;
    width: 90px;
    height: 90px;
    display: inline-block;
  }
  .song > span{
    display: inline-block;
    vertical-align: top;
  }
  .song > span > span,.song > span > a{
    display: block;
  }
  .song > span > .name{
    margin-top: 50%;
  }
  .song > span > .author{

  }
  .song > span > .time{
    margin-left: 400%;
    margin-top: -60%;
  }
  .song> a > i {
    color: silver;
    font-size: 2rem;
    position: absolute;
    top: 50%;
    left: 50%;
    margin-left: -1rem;
    margin-top: -1rem;
    transform: scale(0.01);
    transition: all 1s;
  }
  .song> a:hover > i{
    transform: scale(1);
  }
  .song> a > .notHoverImg{
    transition: all 1s;

  }
  .song> a > .HoverImg{
    transform: scale(1.2);
  }
</style>



