<template>
  <div class="rank" v-on:mouseenter="enter" v-on:mouseleave="leave">
    <h2>巅峰榜</h2>
    <h1>流行指数</h1>
    <div>
      <i class="icon-minus" v-if="!isHover"></i>
      <i v-bind:class="isPlay"></i>
    </div>
    <ul>
      <li>
        <span>1</span>
        <a>骆驼</a>
        <a>薛之谦</a>
      </li>
      <li>
        <span>1</span>
        <p>骆驼</p>
        <p>薛之谦</p>
      </li>
      <li>
        <span>1</span>
        <p>骆驼</p>
        <p>薛之谦</p>
      </li>
      <li>
        <span>1</span>
        <p>骆驼</p>
        <p>薛之谦</p>
      </li>
      <li>
        <span>1</span>
        <p>骆驼</p>
        <p>薛之谦</p>
      </li>
    </ul>
  </div>
</template>
<script>
export default{
  data () {
    return {
      isHover: false,
      isPlay: 'icon-play2 noplay'
    }
  },
  methods: {
    enter: function () {
      this.isHover = true
      this.isPlay = 'icon-play2 noplay play'
    },
    leave: function () {
      this.isHover = false
      this.isPlay = 'icon-play2 noplay'
    }
  }
}
</script>
<style scoped>
  .rank{
    width: 224px;
    height: 500px;
    border: 1px solid #cccccc;
    background-image: url("/static/image/rank_bg.jpg");
    color: white;
    display: inline-block;
    vertical-align: top;
    margin: 0 50px;
  }
  .rank > h2{
    margin-top: 50px;
  }
  .rank > h2, .rank > h1{
    text-align: center;
  }
  .rank >div {
    text-align: center;
    position: relative;
    height: 30px;
    line-height: 30px;
    margin-top: 20px;

  }
  .rank > div > i{
    font-size: 30px;
  }
  .rank > div > .noplay{
    transform: scale(0.01);
    transition: all 0.5s;
    display: inline-block;
    position: absolute;
    left: 50%;
    height: 50%;
    margin-left: -15px;

  }
  .rank > div > .play{
    transform: scale(1.2);
  }
  .rank > ul{
    text-align: left;
    margin-left: 50px;
    list-style: none;

  }
  .rank > ul >li{
    position: relative;
    margin-top: 20px;
  }
  .rank > ul >li > span{
    position: absolute;
    left: -20px;
  }
  .rank > ul >li >a{
    display: block;
  }
</style>



