<template>
  <div class="songSheet">
    <a href="javascript:;" v-on:mouseenter="scaleImg" v-on:mouseleave="scaleImg2">
      <img v-bind:src="gedan.src" width="100%" height="100%" v-bind:class="hoverImg">
      <i class="icon-play2"></i>
    </a>
    <div class="description">{{gedan.description}}</div>
    <div class="popularity">播放量：{{gedan.popularity}}</div>
  </div>
</template>
<script>
  export default{
    data () {
      return {
        hoverImg: 'notHoverImg'
      }
    },
    props: ['gedan'],
    methods: {
      scaleImg: function () {
        this.hoverImg = 'notHoverImg HoverImg'
      },
      scaleImg2: function () {
        this.hoverImg = 'notHoverImg'
      }
    }
  }
</script>
<style scoped>
  .songSheet{
    width: 18%;
    margin: 10px;
  }
  .songSheet > a{
    width: 100%;
    height: 220px;
    overflow: hidden;
    display: inline-block;
    position: relative;

  }
  .songSheet > .description{
    font-size: 15px;
  }
  .songSheet > .popularity{
    font-size: 15px;
    color: #999999;
  }
  .songSheet> a > i {
    color: silver;
    font-size: 4rem;
    position: absolute;
    top: 50%;
    left: 50%;
    margin-left: -2rem;
    margin-top: -2rem;
    transform: scale(0.01);
    transition: all 1s;
  }
  .songSheet> a:hover > i{
    transform: scale(1);
  }
  .songSheet> a > .notHoverImg{
     transition: all 1s;
   }
  .songSheet> a > .HoverImg{
    transform: scale(1.2);
  }

</style>



