<template>
  <div class="music">
    <audio id="myAudio" v-bind:src="$store.state.songList[$store.state.currentIndex].src" ></audio>
    <div class="name">{{$store.state.songList[$store.state.currentIndex].name}}</div>
    <div class="control">
      <i class="icon-previous2" v-on:click="toggleNext('previous')" title="上一首"></i>
      <i v-bind:class="iStyle.playOrPause" v-on:click="toggleState()" v-bind:title="playTitle"></i>
      <i class="icon-next2" v-on:click="toggleNext('next')" title="下一首"></i>
      {{time.cur}}
      <span class="musicTime" >
        <!--<span class="buffered" v-on:click="changeMusicTime2($event)"></span>-->
        <span class="point" >•</span>
        <progress max="100" value="0" v-on:click="changeMusicTime($event)"></progress>
      </span>
      {{$store.state.songList[$store.state.currentIndex].time}}

      <i class="icon-volume-medium" title="音量"></i>
      <span class="voicebox">
        <progress max="100" value="100" class="voice" v-on:click="changeVoice($event)"></progress>
        <span>•</span>
      </span>

      <i v-bind:class="iStyle.loop" v-on:click="toggleLoop()" v-bind:title="loopTitle"></i>
      <a v-bind:href="$store.state.songList[$store.state.currentIndex].src" download class="icon-download3" title="下载"></a>
      <i class="icon-file-text" title="歌单" v-on:click="showList()"></i>

    </div>
    <div class="list" v-if="isShowList">

    </div>
  </div>
</template>
<script>
  import $ from 'jquery'
  function toggleTime (second) {
    let t = Math.ceil(second)
    return (Math.floor(t / 60) / 100).toFixed(2).slice(-2) + ':' + (t % 60 / 100).toFixed(2).slice(-2)
  }
  export default{
    data () {
      return {
        loopTitle: '列表循环',
        playTitle: '播放',
        time: {cur: toggleTime(0), total: this.$store.state.songList[this.$store.state.currentIndex].time, buffered: toggleTime(0)},
        iStyle: {
          playOrPause: 'icon-play3',
          loop: 'icon-loop'
        },
        interval: null,
        isShowList: false
      }
    },
    methods: {
      loop (loopway, index) {
        let length = this.$store.state.songList.length
        if (loopway === '列表循环') {
          let i = index + 1
          if (i === length) {
            i = 0
          }
          return i
        } else if (loopway === '单曲循环') {
          console.log(index)
          return index
        } else if (loopway === '随机播放') {
          let r = Math.round(Math.random() * (length - 1))
          while (r === index) {
            r = Math.round(Math.random() * (length - 1))
          }
          return r
        }
      },
      toggleNext (flag) {
        if (flag === 'previous') {
          if (this.$store.state.currentIndex > 0) {
            this.$store.state.currentIndex --
          } else if (this.$store.state.currentIndex === 0) {
            this.$store.state.currentIndex = this.$store.state.songList.length - 1
          }
        } else if (flag === 'next') {
          let last = this.$store.state.currentIndex
          this.$store.state.currentIndex = this.loop(this.loopTitle, this.$store.state.currentIndex)
          if (last === this.$store.state.currentIndex) {
            const myAudio = $('#myAudio')[0]
            myAudio.currentTime = 0
          }
        }
        setTimeout(() => {
          this.toggleState('icon-play3')
        }, 100)
      },
      toggleState (target) {
        target = target || this.iStyle.playOrPause
        const myAudio = $('#myAudio')[0]
        if (target === 'icon-play3') {
          this.iStyle.playOrPause = 'icon-pause2'
          this.playTitle = '暂停'
          myAudio.play()
          this.interval = setInterval(() => {
            this.time.cur = toggleTime(myAudio.currentTime)
            if (this.time.cur === this.$store.state.songList[this.$store.state.currentIndex].time) {
              clearInterval(this.interval)
              this.toggleNext('next')
            } else {
              let current = (myAudio.currentTime / myAudio.duration).toFixed(2)
              $('.musicTime > progress').eq(0).attr('value', current * 100)
              $('.musicTime > .point').eq(0).css({left: current * 300 + 'px'})
              /* let timeRanges = myAudio.buffered
              let timeBuffered = timeRanges.end(timeRanges.length - 1)
              let bufferedPercent = timeBuffered / myAudio.duration
              $('.music > .control >.musicTime>.buffered').eq(0).css({
                width: bufferedPercent * 300 - current * 300 + 'px',
                left: current * 300 + 2 + 'px'
              }) */
            }
          }, 1000)
        } else if (target === 'icon-pause2') {
          clearInterval(this.interval)
          this.iStyle.playOrPause = 'icon-play3'
          this.playTitle = '播放'
          myAudio.pause()
        }
      },
      toggleLoop () {
        if (this.iStyle.loop === 'icon-loop') {
          this.iStyle.loop = 'icon-loop2'
          this.loopTitle = '单曲循环'
        } else if (this.iStyle.loop === 'icon-loop2') {
          this.iStyle.loop = 'icon-shuffle'
          this.loopTitle = '随机播放'
        } else if (this.iStyle.loop === 'icon-shuffle') {
          this.iStyle.loop = 'icon-loop'
          this.loopTitle = '列表循环'
        }
      },
      changeVoice ($event) {
        const myAudio = $('#myAudio')[0]
        if ($event.offsetX >= 0 && $event.offsetX <= 100) {
          myAudio.volume = $event.offsetX / 100
          $('.music > .control >.voicebox> span').eq(0).css({left: $event.offsetX + 'px'})
          $('.voice').eq(0).attr('value', $event.offsetX)
        }
      },
      changeMusicTime ($event) {
        const myAudio = $('#myAudio')[0]
        if ($event.offsetX >= 0 && $event.offsetX <= 300) {
          /* clearInterval(this.interval) */
          myAudio.currentTime = $event.offsetX / 300 * myAudio.duration
          $('.musicTime > .point').eq(0).css({left: $event.offsetX + 'px'})
          $('.musicTime > progress').eq(0).attr('value', $event.offsetX / 3)
/*          let current = (myAudio.currentTime / myAudio.duration).toFixed(2)
          let timeRanges = myAudio.buffered
          let timeBuffered = timeRanges.end(timeRanges.length - 1)
          let bufferedPercent = timeBuffered / myAudio.duration
          $('.music > .control >.musicTime>.buffered').eq(0).css({
            width: bufferedPercent * 300 - current * 300 + 'px',
            left: current * 300 + 2 + 'px'
          }) */
        }
      },
       /* changeMusicTime2 ($event) {
        const myAudio = $('#myAudio')[0]
        let pro = $('.musicTime > progress').eq(0).attr('value')
        myAudio.currentTime = (pro / 100 + $event.offsetX / 300) * myAudio.duration
        $('.musicTime > .point').eq(0).css({left: pro * 3 + $event.offsetX + 'px'})
        $('.musicTime > progress').eq(0).attr('value', pro + $event.offsetX / 3)
        let current = (myAudio.currentTime / myAudio.duration).toFixed(2)
        let timeRanges = myAudio.buffered
        let timeBuffered = timeRanges.end(timeRanges.length - 1)
        let bufferedPercent = timeBuffered / myAudio.duration
        $('.music > .control >.musicTime>.buffered').eq(0).css({
          width: bufferedPercent * 300 - current * 300 + 'px',
          left: current * 300 + 2 + 'px'
        })
      } */
      showList () {
        if (!this.isShowList) {
          this.isShowList = true
        } else {
          this.isShowList = false
        }
      }
    },
    mounted () {
      /* $.get('http://localhost:8080/', function (json) {
        console.log(json[0])
      }) */
    }
  }
</script>
<style scoped>
  .music{
    font-size: 20px;
    color: #555555;
    position: fixed;
    bottom: 0;
    background: linear-gradient(rgb(220,220,220), rgb(169,169,169));
    width: 100%;
    user-select: none;
    height: 50px;
  }
  .music > .name{
    text-align: center;
    color: #449fdb;
  }
  .music > .control{
    text-align: center;

  }
  .music > .control > i{
    cursor: pointer;
    margin: 0 5px;
  }
  .music > .control > a{
    text-decoration: none;
    color: #555555;
    margin: 0 5px;
  }
  .music > .control >.voicebox{
    position: relative;
  }
  .music > .control >.voicebox> .voice{
    width: 104px;
    height: 10px;
    border-radius: 10px;
    border: 2px solid #e74c3c;
    vertical-align: middle;
    cursor: pointer;
    margin-top: -8px;
  }
  .music > .control >.voicebox> span{
    font-size: 10px;
    line-height: 8px;
    color: #e74c3c;
    position: absolute;
    left: 100px;
    top: 7px;
    width: 10px;
    height: 10px;
    border-radius: 6px;
    background-color: white;
    cursor: pointer;
  }
  .music > .control >.voicebox> .voice::-webkit-progress-bar{
    background-color: white;
  }
  .music > .control >.voicebox> .voice::-webkit-progress-value{
    background-color: #e74c3c;
  }
  .music > .control >.musicTime{
    position: relative;
    cursor: pointer;
    width: 304px;
    height: 26px;
    display: inline-block;
  }
  .music > .control >.musicTime> progress{
    width: 304px;
    height: 10px;
    border-radius: 10px;
    border: 2px solid #e74c3c;
    vertical-align: middle;
    cursor: pointer;
    margin-top: -8px;
  }
  .music > .control >.musicTime> progress::-webkit-progress-bar{
    background-color: white;
  }
  .music > .control >.musicTime> progress::-webkit-progress-value{
    background-color: #e74c3c;
  }
  .music > .control >.musicTime>.point{
    font-size: 10px;
    line-height: 8px;
    color: #e74c3c;
    position: absolute;
    left: 0;
    top: 7px;
    width: 10px;
    height: 10px;
    border-radius: 6px;
    background-color: white;
    cursor: pointer;
  }
  .music > .control >.musicTime>.buffered{
    position: absolute;
    left: 2px;
    top: 9px;
    width: 0;
    height: 6px;
    background-color: #c9c9c9;

  }
  .music > .list{
    width: 70%;
    height: 300px;
    border: 1px solid red;
    position: absolute;
    top: -300px;
    left: 15%;

  }
</style>



