<template>
  <div>
    <a href="javascript:;" class="image"><img v-bind:src="singer.img" width="100%" height="100%"/></a>
    <a href="javascript:;" class="name">{{singer.name}}</a>
  </div>
</template>
<script>
export default{
  data () {
    return {}
  },
  props: ['singer']
}
</script>
<style scoped>
  div{
    width: 140px;
    height: 200px;
    text-align: center;
    margin: 20px auto;
  }
  div > a{
    display: block;
    text-decoration: none;
  }
  div > .image{
    border-radius: 70px;
    width: 140px;
    height: 140px;
    overflow: hidden;
  }
  div > .name{
    margin-top: 20px;
    color: #555555;
  }
</style>



