<template>
  <div class="classify">
    <div class="d1" v-if="classify.title">
    <span >{{classify.title}}</span>
    </div>
    <div class="d2">
    <a href="javascript:;" class="current" v-for="(item,index) in classify.content" v-on:click="toggle(index)"
       v-if="item === current.cur">{{item}}</a>
    <a href="javascript:;" class="notcurrent" v-else v-on:click="toggle(index)">{{item}}</a>
    </div>
  </div>
</template>
<script>
export default{
  data () {
    return {
    }
  },
  props: ['classify', 'current'],
  methods: {
    toggle (index) {
      if (this.current.cur !== this.classify.content[index]) {
        this.current.cur = this.classify.content[index]
      }
    }
  }
}
</script>
<style scoped>
  .classify{
    margin: 20px 0;
    display: flex;
  }
  .classify >div> a, .classify >div> span{
    text-decoration: none;
    color: #555555;
    margin: 0 15px;
    display: inline-block;
    font-size: 14px;
    padding: 5px;
  }
  .classify > .d1{
    flex: 1;
  }
  .classify > .d2{
    flex: 14;
  }
  .classify >div> .current {
    background-color: rgb(49,194,124);
    color: white;

  }
</style>



