<template>
  <div class="rankSong" v-on:mouseenter="toggle(isShow)" v-on:mouseleave="toggle(isShow)">
    <div class="index red" v-if="rankSong.index < 4">{{rankSong.index}}</div>
    <div class="index" v-else>{{rankSong.index}}</div>
    <div class="image"><img v-bind:src="rankSong.image" height="70px"></div>
    <div class="name">
      {{rankSong.name}}
      <span v-if="isShow">
        <span class="icon-play2" title="播放"></span>
        &nbsp;&nbsp;
        <span class="icon-plus" title="添加到歌单"></span>
        &nbsp;&nbsp;
        <span class="icon-download3" title="下载"></span>
      </span>
    </div>
    <div class="singer">{{rankSong.singer}}</div>
    <div class="time">{{rankSong.time}}</div>
  </div>
</template>
<script>
  export default{
    data () {
      return {
        isShow: false
      }
    },
    props: ['rankSong'],
    methods: {
      toggle (flag) {
        if (flag) {
          this.isShow = false
        } else {
          this.isShow = true
        }
      }
    }
  }
</script>
<style scoped>
  .rankSong{
    display: flex;
    height: 70px;
    margin: 5px 0;
  }
  .rankSong > div{
    text-align: center;
    margin: 10px;
    vertical-align: middle;
    display: table-cell;
    line-height: 80px;
  }
  .rankSong > .index{
    flex: 1;
    font-size: 25px;
  }
  .rankSong > .red{
    color: red;
  }
  .rankSong > .image{
    flex: 2;
    text-align: right;
  }
  .rankSong > .name{
    flex: 9;
    text-align: left;
    position: relative;
  }
  .rankSong > .name > span{
    position: absolute;
    right: 0;
    color: silver;
    font-size: 20px;
  }
  .rankSong > .name > span >span{
    cursor: pointer;
  }
  .rankSong > .singer{
    flex: 4;
  }
  .rankSong > .time{
    flex: 1;
  }
</style>



