import Vue from 'vue'
import Router from 'vue-router'
import musicRoom from '@/components/musicRoom'
import myMusic from '@/components/myMusic'
import homePage from '@/components/homePage'
import singer from '@/components/singer'
import album from '@/components/album'
import rank from '@/components/rank'
import classify from '@/components/classify'
import MV from '@/components/MV'
import singerMessage from '@/components/singer/singerMessage'
import songMessage from '@/components/playMusic/songMessage'
import gedanMessage from '@/components/playMusic/gedanMessage'
import mvMessage from '@/components/playMusic/mvMessage'
import albumMessage from '@/components/playMusic/albumMessage'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'musicRoom',
      component: musicRoom,
      children: [{
        path: '/',
        name: 'index',
        component: homePage
      },
      {
        path: '/singer',
        name: 'singer',
        component: singer
      },
      {
        path: '/singermessage',
        name: 'singermessage',
        component: singerMessage
      },
      {
        path: '/album',
        name: 'album',
        component: album
      },
      {
        path: '/rank',
        name: 'rank',
        component: rank
      },
      {
        path: '/classify',
        name: 'classify',
        component: classify
      },
      {
        path: '/mv',
        name: 'MV',
        component: MV
      },
      {
        path: '/songmessage',
        name: 'songMessage',
        component: songMessage
      },
      {
        path: '/gedanmessage',
        name: 'gedanMessage',
        component: gedanMessage
      },
      {
        path: '/mvmessage',
        name: 'mvMessage',
        component: mvMessage
      },
      {
        path: '/albummessage',
        name: 'albumMessage',
        component: albumMessage
      }]
    },
    {
      path: '/mymusic',
      name: 'myMusic',
      component: myMusic
    }
  ]
})



