<template>
  <div class="album">
    <a href="javascript:;" v-on:mouseenter="scaleImg" v-on:mouseleave="scaleImg2">
      <img src="/static/image/mv1.jpg" v-bind:class="hoverImg" width="100%" height="100%">
      <i class="icon-play2"></i>
    </a>
    <div>name</div>
    <div>author</div>
  </div>
</template>
<script>
  export default{
    data () {
      return {
        hoverImg: 'notHoverImg'
      }
    },
    props: ['gedan'],
    methods: {
      scaleImg: function () {
        this.hoverImg = 'notHoverImg HoverImg'
      },
      scaleImg2: function () {
        this.hoverImg = 'notHoverImg'
      }
    }
  }
</script>
<style scoped>
  .album{
    flex: 1;
    margin: 10px;
  }
  .album > a{
    display: block;
    width: 100%;
    height: 125px;
    position: relative;
    overflow: hidden;
  }
  .album> a > i {
    color: silver;
    font-size: 4rem;
    position: absolute;
    top: 50%;
    left: 50%;
    margin-left: -2rem;
    margin-top: -2rem;
    transform: scale(0.01);
    transition: all 1s;
  }
  .album> a:hover > i{
    transform: scale(1);
  }
  .album> a > .notHoverImg{
    transition: all 1s;

  }
  .album> a > .HoverImg{
    transform: scale(1.2);
  }
</style>



