<template>
  <div class="gedanSong" v-on:mouseenter="toggle(isShow)" v-on:mouseleave="toggle(isShow)">
    <div class="index">{{gedanSong.index}}</div>
    <div class="name">
      {{gedanSong.name}}
      <span v-if="isShow">
        <span class="icon-play2" title="播放"></span>
        &nbsp;&nbsp;
        <span class="icon-plus" title="添加到歌单"></span>
        &nbsp;&nbsp;
        <span class="icon-download3" title="下载"></span>
      </span>
    </div>
    <div class="singer">{{gedanSong.singer}}</div>

    <div class="time">{{gedanSong.time}}</div>
  </div>
</template>
<script>
  export default{
    data () {
      return {
        isShow: false
      }
    },
    props: ['gedanSong'],
    methods: {
      toggle (flag) {
        if (flag) {
          this.isShow = false
        } else {
          this.isShow = true
        }
      }
    }
  }
</script>
<style scoped>
  .gedanSong{
    display: flex;
    height: 60px;
    margin: 5px 0;
  }
  .gedanSong > div{
    text-align: center;
    margin: 10px;
    vertical-align: middle;
    display: table-cell;
    line-height: 60px;
  }
  .gedanSong > .index{
    flex: 1;
    font-size: 25px;
  }
  .gedanSong > .name{
    flex: 3;
    text-align: left;
    position: relative;
  }
  .gedanSong > .name > span{
    position: absolute;
    right: 0;
    color: silver;
    font-size: 20px;
  }
  .gedanSong > .name > span >span{
    cursor: pointer;
  }
  .gedanSong > .singer{
    flex: 2;
  }
  .gedanSong > .time{
    flex: 1;
  }
</style>



