<template>
  <div class="nav">
    <a href="/#/" class="navIcon"><img src="/static/image/timg.jpg" width="200px" height="80px"></a>
    <a href="/#/" class="navTextSelect" v-if="current === 0" v-on:click="toggle(0)">音乐馆</a>
    <a href="/#/" class="navText" v-if="current === 1" v-on:click="toggle(0)">音乐馆</a>
    <a href="/#/mymusic" class="navTextSelect" v-if="current === 1" v-on:click="toggle(1)">我的音乐</a>
    <a href="/#/mymusic" class="navText" v-if="current === 0" v-on:click="toggle(1)">我的音乐</a>
    <form action="#" id="search">
      <input placeholder="提示信息"/>
      <a href="javascript:;" v-on:click="submit"><i class="icon-search"></i></a>
    </form>
    <a href="javascript:;" class="navText" v-on:click="showLogin">登录</a>
    <div class="space"></div>
    <modal v-bind:isShowModal=showModal></modal>
  </div>
</template>
<script>
import modal from './modal.vue'
export default {
  name: 'navigation',
  methods: {
    submit: function () {
      document.getElementById('search').submit()
    },
    showLogin: function () {
      this.showModal.state = true
    },
    toggle: function (flag) {
      if (this.current !== flag) {
        this.current = flag
      }
    }
  },
  components: {
    modal
  },
  data () {
    return {
      showModal: {
        state: false
      },
      current: 0
    }
  }
}
</script>
<style scoped>
  .nav{
    white-space: nowrap;
    display: flex;
    -webkit-justify-content: center;
  }
  .nav > form{
    display: inline-block;
    height: 80px;
    line-height: 80px;
    flex: 5;
    text-align: right;
  }
  .nav > form > input{
    height: 30px;
    width: 200px;
    font-size: 15px;
    border-radius: 5px;
    border: 1px solid #cccccc;
    outline: none;
    padding-right: 25px;
  }
  .nav > form > a{
    margin-left: -30px;
    text-decoration: none;
    color: #cccccc;
  }
  .nav > form > a:hover{
    color: rgb(49,194,124);
  }
  .nav > form > a > i{
    font-size: 18px;
  }
  .nav > a{
    line-height: 80px;
    font-size: 20px;
    text-decoration: none;
    height: 80px;
    padding: 0 20px;
    vertical-align: top;
    color: black;
    flex: 0.5;
    text-align: center;
  }
  .nav > .navIcon{
    flex: 2;
    text-align: right;
  }
  .nav > .navTextSelect{
    background-color: rgb(49,194,124);
    color: white;
  }
  .nav > .navText:hover{
    color: rgb(49,194,124);
  }
  .nav > .space{
    flex: 1;
  }
</style>



