<template>
  <div>

    <span v-if="page.current < 6">
      <a href="javascript:;" v-for="item in arrFirst" v-if="item === page.current" v-on:click="turnTo(item)"
         class="current" >{{item}}</a>
      <a href="javascript:;"  v-else v-on:click="turnTo(item)">{{item}}</a>
      <span>...</span>
      <a href="javascript:;" v-on:click="turnTo(page.total)">{{page.total}}</a>
      <a href="javascript:;" v-on:click="turn('right')"><span class="icon-circle-right"></span></a>
    </span>

    <span v-if="page.current >= 6 && page.current <= page.total - 6">
      <a href="javascript:;" v-on:click="turn('left')"><span class="icon-circle-left"></span></a>
      <a href="javascript:;" v-on:click="turnTo(1)">{{1}}</a>
      <span>...</span>
      <a href="javascript:;" v-on:click="turnTo(page.current - 2)">{{page.current - 2}}</a>
      <a href="javascript:;" v-on:click="turnTo(page.current - 1)">{{page.current - 1}}</a>
      <a href="javascript:;" class="current" v-on:click="turnTo(page.current)">{{page.current}}</a>
      <a href="javascript:;" v-on:click="turnTo(page.current + 1)">{{page.current + 1}}</a>
      <a href="javascript:;" v-on:click="turnTo(page.current + 2)">{{page.current + 2}}</a>
      <span>...</span>
      <a href="javascript:;" v-on:click="turnTo(page.total)">{{page.total}}</a>
      <a href="javascript:;" v-on:click="turn('right')"><span class="icon-circle-right"></span></a>
    </span>

    <span v-if="page.current > page.total - 6">
      <a href="javascript:;" v-on:click="turn('left')"><span class="icon-circle-left"></span></a>
      <a href="javascript:;" v-on:click="turnTo(1)">{{1}}</a>
      <span>...</span>
      <a href="javascript:;" v-for="item in arrEnd" v-if="item === page.current" v-on:click="turnTo(item)"
         class="current">{{item}}</a>
      <a href="javascript:;"  v-else v-on:click="turnTo(item)">{{item}}</a>
    </span>


  </div>
</template>
<script>
export default{
  data () {
    const arr = []
    for (let i = this.page.total - 5; i <= this.page.total; i++) {
      arr.push(i)
    }
    return {
      arrFirst: [1, 2, 3, 4, 5],
      arrEnd: arr
    }
  },
  props: ['page'],
  methods: {
    turn (flag) {
      if (this.page.current > 1 && flag === 'left') {
        this.page.current--
      } else if (this.page.current < this.page.total && flag === 'right') {
        this.page.current++
      }
    },
    turnTo (target) {
      this.page.current = target
    }
  }
}
</script>
<style scoped>
  div{
    text-align: center;
  }
  div a{
    display: inline-block;
    text-decoration: none;
    color: rgb(185,185,185);
    font-size: 20px;
    margin: 10px;
    padding: 10px 15px;
  }
  .current{
    color: white;
    background-color: rgb(49,194,124);
  }
</style>



