<template>
  <div class="album">
    <a href="javascript:;" v-on:mouseenter="scaleImg" v-on:mouseleave="scaleImg2">
      <img v-bind:src="album.src" width="100%" height="100%" v-bind:class="hoverImg">
      <i class="icon-play2"></i>
    </a>
    <div class="name"><a>{{album.name}}</a></div>
    <div class="author"><a>{{album.author}}</a></div>
    <div class="time"><a>{{album.time}}</a></div>
  </div>
</template>
<script>
  export default{
    data () {
      return {
        hoverImg: 'notHoverImg'
      }
    },
    props: ['album'],
    methods: {
      scaleImg: function () {
        this.hoverImg = 'notHoverImg HoverImg'
      },
      scaleImg2: function () {
        this.hoverImg = 'notHoverImg'
      }
    }
  }
</script>
<style scoped>
  .album{
    width: 18%;
    margin: 10px;
  }
  .album > a{
    width: 100%;
    height: 220px;
    overflow: hidden;
    display: inline-block;
    position: relative;

  }
  .album > .name,.album > .author,.album > .time{
    font-size: 15px;
    color: #555555;
  }
  .album > .author,.album > .time{
    color: rgb(165,153,178);
  }
  .album> a > i {
    color: silver;
    font-size: 4rem;
    position: absolute;
    top: 50%;
    left: 50%;
    margin-left: -2rem;
    margin-top: -2rem;
    transform: scale(0.01);
    transition: all 1s;
  }
  .album> a:hover > i{
    transform: scale(1);
  }
  .album> a > .notHoverImg{
    transition: all 1s;

  }
  .album> a > .HoverImg{
    transform: scale(1.2);
  }

</style>



