<template>
  <div class="albums" v-on:mouseenter="toggleArrow" v-on:mouseleave="toggleArrow">
    <a href="javascript:;" class="icon-circle-left iconLeft" v-if="!arrow" v-on:click="moveLeft"></a>
    <a href="javascript:;" class="icon-circle-left iconLeft iconLeftShow" v-else v-on:click="moveLeft"></a>
    <a href="javascript:;" class="icon-circle-right iconRight" v-if="!arrow" v-on:click="moveRight"></a>
    <a href="javascript:;" class="icon-circle-right iconRight iconRightShow" v-else v-on:click="moveRight"></a>
    <div class="pagebox">
      <div v-bind:class="pageClass" >
        <album></album>
        <album></album>
        <album></album>
        <album></album>
        <album></album>
        <album></album>
        <album></album>
        <album></album>
        <album></album>
        <album></album>
      </div>
      <div v-bind:class="pageClass" >
        <album></album>
        <album></album>
        <album></album>
        <album></album>
        <album></album>
        <album></album>
        <album></album>
        <album></album>
        <album></album>
        <album></album>
      </div>

      <div class="circleControl">
        <a v-for="index in arrPage" class="currentCircleControl" v-if="index===currentPage-1" v-on:click="toggleGedan(index)"></a>
        <a v-else v-on:click="toggleGedan(index)"></a>
      </div>
    </div>
  </div>
</template>
<script>
  import album from './album.vue'
  export default{
    data () {
      return {
        arrow: false,
        currentPage: 1,
        totalPage: 2,
        arrPage: [],
        pageClass: 'page'
      }
    },
    mounted () {
      for (let i = 0; i < this.totalPage; i++) {
        this.arrPage.push(i)
      }
    },
    props: ['current', 'totalGedan'],
    components: {
      album
    },
    methods: {
      toggleArrow: function () {
        if (this.arrow === false) {
          this.arrow = true
        } else {
          this.arrow = false
        }
      },
      toggleGedan: function (index) {
        this.currentPage = index + 1
        this.pageClass = 'page page' + this.currentPage
      },
      moveLeft: function () {
        this.currentPage--
        if (this.currentPage === 0) {
          this.currentPage = this.totalPage
        }
        this.pageClass = 'page page' + this.currentPage
      },
      moveRight: function () {
        this.currentPage++
        if (this.currentPage === this.totalPage + 1) {
          this.currentPage = 1
        }
        this.pageClass = 'page page' + this.currentPage
      }
    }
  }
</script>
<style scoped>
  .albums{
    padding: 10px 5%;
    width: 100%;
  }
  .albums > .pagebox{
    width: 90%;
    overflow: hidden;
    height: 670px;
  }
  .albums  >.pagebox> .page{
    display: flex;
    width: 200%;
    margin-left: 0;
    transition: all 1s;
  }
  .albums  >.pagebox> .page1{
    margin-left: 0;
  }
  .albums  >.pagebox> .page2{
    margin-left: -100%;
  }
  .albums > .pagebox > .circleControl{
    position: absolute;
    bottom: 5%;
    left: 50%;
    margin-left: -25px;
  }
  .albums > .pagebox > .circleControl > a{
    cursor: pointer;
    display: inline-block;
    width: 15px;
    height: 15px;
    border: 1px solid rgb(49,194,124);
    border-radius: 50%;
    margin: 5px;

  }
  .albums > .pagebox > .circleControl > .currentCircleControl{
    background-color: rgb(49,194,124);
  }
  .albums > .iconLeft, .albums > .iconRight{
    font-size: 50px;
    color: #cccccc;
    position: absolute;
    top: 50%;
    margin-top: -25px;
    transition: all 1s;
    text-decoration: none;
  }
  .albums > .iconLeft{
    left: -50px;
  }
  .albums > .iconLeftShow{
    left: 0;
  }
  .albums > .iconRight{
    right: -50px;
  }
  .albums > .iconRightShow{
    right: 0;
  }
</style>



