// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import App from './App'
import router from './router'
import vuex from 'vuex'

Vue.config.productionTip = false
Vue.use(vuex)
var store = new vuex.Store({
  state: {
    songList: [{
      name: '三角题',
      author: '二珂',
      time: '04:25',
      src: '/static/music/二珂 - 三角题.mp3'
    }, {name: 'faded',
      author: 'Alan Walker',
      time: '03:32',
      src: '/static/music/Alan Walker - Faded.mp3'
    }, {name: '凉凉',
      author: '杨宗纬 张碧晨',
      time: '05:33',
      src: '/static/music/杨宗纬 张碧晨 - 凉凉.mp3'
    }, {name: '告白气球',
      author: '周杰伦',
      time: '03:35',
      src: '/static/music/周杰伦 - 告白气球.mp3'
    }, {name: '成全',
      author: '林宥嘉',
      time: '05:07',
      src: '/static/music/成全-林宥嘉.mp3'}],
    currentIndex: 0
  }
})
/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  store,
  template: '<App/>',
  components: { App }
})



