<template>
  <div class="singer">
    <classify v-bind:classify="classify.type1" v-bind:current="current[0]"></classify>
    <classify v-bind:classify="classify.type2" v-bind:current="current[1]"></classify>
    <h2>全部歌手</h2>
    <ul v-if="page.current === 1">
      <li><singer v-bind:singer="singerPage0[0]"></singer></li>
      <li><singer v-bind:singer="singerPage0[0]"></singer></li>
      <li><singer v-bind:singer="singerPage0[0]"></singer></li>
      <li><singer v-bind:singer="singerPage0[0]"></singer></li>
      <li><singer v-bind:singer="singerPage0[0]"></singer></li>

      <li><singer v-bind:singer="singerPage0[0]"></singer></li>
      <li><singer v-bind:singer="singerPage0[0]"></singer></li>
      <li><singer v-bind:singer="singerPage0[0]"></singer></li>
      <li><singer v-bind:singer="singerPage0[0]"></singer></li>
      <li><singer v-bind:singer="singerPage0[0]"></singer></li>
    </ul>
    <ul class="singerName">
      <li><a v-bind:href="singer[0].src">{{singer[0].name}}</a></li>
      <li><a v-bind:href="singer[0].src">{{singer[0].name}}</a></li>
      <li><a v-bind:href="singer[0].src">{{singer[0].name}}</a></li>
      <li><a v-bind:href="singer[0].src">{{singer[0].name}}</a></li>
      <li><a v-bind:href="singer[0].src">{{singer[0].name}}</a></li>
      <li><a v-bind:href="singer[0].src">{{singer[0].name}}</a></li>
      <li><a v-bind:href="singer[0].src">{{singer[0].name}}</a></li>
      <li><a v-bind:href="singer[0].src">{{singer[0].name}}</a></li>
      <li><a v-bind:href="singer[0].src">{{singer[0].name}}</a></li>
      <li><a v-bind:href="singer[0].src">{{singer[0].name}}</a></li>
    </ul>
    <page v-bind:page="page"></page>
    <footer></footer>
  </div>
</template>
<script>
import classify from './singer/classify.vue'
import singer from './singer/singer.vue'
import page from './singer/page.vue'
export default{
  data () {
    const arr = []
    arr.push('全部')
    for (let i = 65; i < 91; i++) {
      arr.push(String.fromCharCode(i))
    }
    arr.push('#')
    return {
      classify: {
        type1: {
          title: '',
          content: ['全部', '华语男', '华语女', '华宇组合', '韩国男', '韩国女', '韩国组合', '日本男', '日本女',
            '日本组合', '欧美男', '欧美女', '欧美组合', '乐团', '演奏家', '作曲家', '指挥家', '其他']},
        type2: {title: '', content: arr}
      },
      current: [{cur: '全部'}, {cur: '全部'}],
      page: {total: 50, current: 1},
      singerPage0: [{img: '/static/image/singer.jpg', name: '薛之谦', src: ''}],
      singer: [{name: '张杰', src: '/#/singer000'}]
    }
  },
  components: {
    classify,
    singer,
    page
  }
}
</script>
<style scoped>
  .singer{
    margin: 0 100px;
  }
  .singer > ul{
    display: flex;
    flex-wrap: wrap;
    list-style: none;
  }
  .singer > ul > li{
    width: 20%;
  }
  .singer > .singerName > li{
    margin: 10px 0;
  }
  .singer > .singerName > li > a{
    text-decoration: none;
    font-size: 15px;
  }
  footer{
    height: 100px;
    margin: 0 -100px;
    background-color: rgb(51,51,51);
  }
</style>



