<template>
  <div id="smallNav">
    <span class="start"></span>
    <a v-for="(item, index) in title" class="nav2" v-if="current !== index" v-bind:href="item.url" v-on:click="toggle(index)">
      <span>{{item.name}}</span>
    </a>
    <a class="nav2 currentNav" v-else v-bind:href="item.url" v-on:click="toggle(index)">
      <span>{{item.name}}</span></a>

    <span class="end"></span>
  </div>
</template>
<script>
export default {
  name: 'smallNav',
  data () {
    return {
      title: [{name: '首页', url: '/#/'}, {name: '歌手', url: '/#/singer'}, {name: '专辑', url: '/#/album'},
        {name: '排行榜', url: '/#/rank'}, {name: '分类歌单', url: '/#/classify'}, {name: 'MV', url: '/#/mv'}],
      current: 0
    }
  },
  methods: {
    toggle: function (flag) {
      if (this.current !== flag) {
        this.current = flag
      }
    }
  }
}
</script>
<style scoped>
  #smallNav{
    display: flex;
    border: 1px solid #cccccc;
    height: 40px;
    line-height: 40px;
  }
  #smallNav > .nav2{
    flex: 1;
    text-align: center;
    text-decoration: none;
    color: #555555;
    font-size: 16px;

  }
  #smallNav > .nav2 > span:hover{
    color: rgb(49,194,124);
  }
  #smallNav > .start, #smallNav > .end{
    flex: 3;
  }
  #smallNav > .currentNav{
    color: rgb(49,194,124);
  }
</style>



