<template>
  <div id="rank">
    <ul>
      <li v-for="(item,index) in rankTitle" v-if="index === current" class="current" v-on:click="toggle(index)">{{item}}</li>
      <li v-else v-on:click="toggle(index)">{{item}}</li>
    </ul>
    <div>
      <h2>{{rankTitle[0]}}</h2>
      <div class="operation">
        <a href="javascript:;">播放全部</a>
        <a href="javascript:;">添加到</a>
      </div>
      <ul class="tableHead">
        <li class="song">歌曲</li>
        <li class="singer">歌手</li>
        <li class="time">时长</li>
      </ul>
      <ul class="tableContent">
        <li><rankSong v-bind:rankSong="rankSong[0]"></rankSong></li>
        <li><rankSong v-bind:rankSong="rankSong[0]"></rankSong></li>
        <li><rankSong v-bind:rankSong="rankSong[0]"></rankSong></li>
        <li><rankSong v-bind:rankSong="rankSong[0]"></rankSong></li>
        <li><rankSong v-bind:rankSong="rankSong[0]"></rankSong></li>
        <li><rankSong v-bind:rankSong="rankSong[0]"></rankSong></li>
      </ul>

    </div>
    <footer></footer>
  </div>
</template>
<script>
import rankSong from './singer/rankSong.vue'
export default{
  data () {
    return {
      rankTitle: ['巅峰榜·热歌', '巅峰榜·新歌', '巅峰榜·内地', '巅峰榜·港台', '巅峰榜·欧美', '巅峰榜·韩国', '巅峰榜·日本'],
      current: 0,
      rankSong: [{index: 1,
        image: '/static/image/rankSong1.jpg',
        name: '天后（Live）',
        singer: '薛之谦',
        time: '04:07'}]
    }
  },
  methods: {
    toggle (index) {
      if (this.current !== index) {
        this.current = index
      }
    }
  },
  components: {
    rankSong
  }
}
</script>
<style scoped>
  #rank{
    margin: 0 100px;
  }
  #rank > ul{
    display: inline-block;
    border: 1px solid rgb(229,229,229);
    list-style: none;
    width: 15%;
  }
  #rank > ul > li{
    font-size: 20px;
    line-height: 40px;
    color: #555555;
    cursor: pointer;
  }
  #rank > ul > .current{
    background-color: rgb(49,194,124);
    color: white;
  }
  #rank > div{
    width: 80%;
    display: inline-block;
    vertical-align: top;
    margin: 50px 0;
  }
  #rank > div > h2{
    margin: 20px;
  }
  #rank > div > .operation > a{
    margin: 20px;
    display: inline-block;
    width: 100px;
    padding: 10px;
    border: 1px solid rgb(201,201,201);
    text-align: center;
    text-decoration: none;
    color: #555555;
    border-radius: 5px;
  }
  #rank > div > .operation > a:hover{
    background-color: rgb(237,237,237);
  }
  #rank > div > .tableHead{
    display: flex;
  }
  #rank > div > .tableHead > li{
    display: inline-block;
    list-style: none;
    text-align: center;
    color: #999999;
  }
  #rank > div > .tableHead >.song{
    flex: 11;
    text-align: left;
    padding-left: 12%;
  }
  #rank > div > .tableHead >.singer{
    flex: 4;
    margin-left: -15%;
  }
  #rank > div > .tableHead >.time{
    flex: 1;
  }
  #rank > div > .tableContent{
    list-style: none;
  }
  footer{
    height: 100px;
    background-color: rgb(51,51,51);
    margin: 0 -100px;
  }
</style>



