<template>
  <div id="classify">
    <classify v-bind:classify="classify.type1" v-bind:current="current[0]"></classify>
    <h2>全部歌单</h2>
    <div class="gedans">
      <songSheet v-bind:gedan="gedan[0]"></songSheet>
      <songSheet v-bind:gedan="gedan[0]"></songSheet>
      <songSheet v-bind:gedan="gedan[0]"></songSheet>
      <songSheet v-bind:gedan="gedan[0]"></songSheet>
      <songSheet v-bind:gedan="gedan[0]"></songSheet>

      <songSheet v-bind:gedan="gedan[0]"></songSheet>
      <songSheet v-bind:gedan="gedan[0]"></songSheet>
      <songSheet v-bind:gedan="gedan[0]"></songSheet>
      <songSheet v-bind:gedan="gedan[0]"></songSheet>
      <songSheet v-bind:gedan="gedan[0]"></songSheet>
    </div>
    <page v-bind:page="page"></page>
    <footer></footer>
  </div>
</template>
<script>
import classify from './singer/classify.vue'
import page from './singer/page.vue'
import songSheet from './homePage/songSheet.vue'
export default{
  data () {
    return {
      classify: {
        type1: {
          title: '类别',
          content: ['全部', '经典', '网络歌曲', 'KTV热歌', '中国风', '伤感', '安静', '治愈', '励志',
            '寂寞', '睡前', '学习', '开车', '运动', '旅行', '摇滚', '流行', '其他']}
      },
      current: [{cur: '全部'}, {cur: '全部'}, {cur: '全部'}],
      page: {total: 50, current: 1},
      gedan: [
        {src: '/static/image/gedan1.jpg', description: '描述1', popularity: '人气'},
        {src: '/static/image/gedan2.jpg', description: '描述2', popularity: '人气'},
        {src: '/static/image/gedan3.jpg', description: '描述3', popularity: '人气'},
        {src: '/static/image/gedan4.jpg', description: '描述4', popularity: '人气'}
      ]
    }
  },
  components: {
    classify,
    page,
    songSheet
  }
}
</script>
<style scoped>
  #classify{
    margin: 0 100px;
  }
  #classify> .gedans{
    display: flex;
    flex-wrap: wrap;
  }
  footer{
    height: 100px;
    margin: 0 -100px;
    background-color: rgb(51,51,51);
  }
</style>



